import React, { useEffect } from 'react'
import {motion} from 'framer-motion'
import { ContactContainer, FormGroup } from '../../styles/contactUsStyles'
import { Container, Button, DefaultSizes, VerticalPadding } from '../../styles/globalStyles'

// Context
// import {useGlobalStateContext, useGlobalDispatchContext} from '../../context/globalContext'
//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'

import {parent, child} from '../../animations/animations'


import firebase from "gatsby-plugin-firebase"


const Contact = () => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
        rootMargin: '-100px',
    })

    

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        };

        const evList = () => {
            const form = document.querySelector('form');
            form.addEventListener('submit', (e) => {
                console.log(form.name.value)
                console.log(form.email.value)
                console.log(form.message.value)
                
                
                const db = firebase.firestore()

            

                db.collection("emails").add({
                    name: form.name.value,
                    email: form.email.value,
                    message: form.message.value,
                    time: new Date(Date.now())
                }).then(() => {
                    alert('Submetido com sucesso! Iremos contactar o mais brevemente possível!');

                    document.getElementById("name").value = "";
                    document.getElementById("email").value = "";
                    document.getElementById("message").value = "";
                    // window.location.reload();
                    window.location.href = "https://rodrigorochadev.github.io/pathfinderdesigns/";
                }).catch(() => {
                    alert('Erro ao submeter. Por favor tente novamente. Se o erro persistir, tente mais tarde, ou contacte-nos.')
                });
                e.preventDefault();
                e.stopImmediatePropagation()
            
            });
        }
        evList();
    }, [animation, inView])

    return(
        <ContactContainer>
            <Container ref={contentRef} 
                animate={animation} 
                initial='hidden' 
                variants={parent}
            >
                <VerticalPadding>
                    <>
                    
                    </>
                    <DefaultSizes black>
                        <motion.h1 variants={child}>Fale connosco.</motion.h1>
                        <motion.p variants={child}>Tire dúvidas, proponha ideias. Teremos todo o gosto em ajudar e responderemos o mais rapidamente possível.</motion.p>
                        <motion.p variants={child}>Caso prefira, pode também comunicar pelas nossas redes sociais.</motion.p>
                        <br />
                    </DefaultSizes>
                
                
                

                <motion.form  variants={child} id="form">
                    <FormGroup>
                        <label htmlFor="name">
                            <p>Nome</p>
                            <input type="text" id="name" required />
                        </label>
                    </FormGroup>
                    <FormGroup className="form-group">
                        <label htmlFor="name">
                            <p>Email</p>
                            <input type="email" id="email" required/>
                        </label>
                    </FormGroup>
                    <FormGroup className="form-group">
                        <label htmlFor="name">
                            <p>Mensagem</p>
                            <textarea type="text" id="message" required></textarea>
                        </label>
                    </FormGroup>
                    
                    <Button>
                        <button>Enviar</button>
                    </Button>
                    
                    
                </motion.form>

                </VerticalPadding>
            </Container>
            
            
        </ContactContainer>
    );
}

export default Contact