import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

import HomeTitle from '../components/homepage/homeTitle'
import VideoCaption from '../components/videoCaption/videoCaption'
import Contact from '../components/contact/contactUs'

import captionVideo from '../assets/video/portfolio.mp4'

const Portfolio = () => (
  <Layout>
    <SEO title="Portfolio" />
    <HomeTitle title="o nosso trabalho." leading="@pathfinderdesigns" />
    <VideoCaption video={captionVideo} caption="Esta página encontra-se em construção. Por favor volte mais tarde." />
    <Contact />
  </Layout>
)

export default Portfolio
